<template>
    <div class="yingyong" v-if="indexshow">
        <headerBar :title="headerNavData"></headerBar>
        <yd-flexbox class="price">
            <!-- <yd-flexbox-item class="price_m">
              <h1>开盘价</h1>
              <span class="green">--</span>
            </yd-flexbox-item> -->
            <yd-flexbox-item class="price_m">
              <h1>{{$t('data.highestprice')}}</h1>
              <span class="red">{{heigh}}</span>
            </yd-flexbox-item>
            <yd-flexbox-item class="price_m">
              <h1>{{$t('data.lowestprice')}}</h1>
              <span class="green">{{low}}</span>
            </yd-flexbox-item>
            <yd-flexbox-item class="price_m">
              <h1>{{$t('data.volume')}}</h1>
              <span>{{volume}}</span>
            </yd-flexbox-item>
        </yd-flexbox>
        <yd-flexbox class="hq_nav">
            <yd-flexbox-item><span @click="tabnar('m')" :class="{spanbj:navbj=='m'}">M</span></yd-flexbox-item>
            <yd-flexbox-item><span @click="tabnar('h')" :class="{spanbj:navbj=='h'}">H</span></yd-flexbox-item>
            <yd-flexbox-item><span @click="tabnar('d')" :class="{spanbj:navbj=='d'}">D</span></yd-flexbox-item>
            <yd-flexbox-item><span @click="tabnar('w')" :class="{spanbj:navbj=='w'}">W</span></yd-flexbox-item>
        </yd-flexbox>
        <div id="myChart" :style="{width: '100%', height: '5rem'}"></div>
    </div>
  
</template>

<script>
  import {getCookie,setCookie,getCookieWithDefault} from '../assets/js/cookie.js'
  import headerBar from './header.vue'
export default {
  beforeCreate () {
    setTimeout(function(){
    document.querySelector('body').setAttribute('style', 'background:#0f1418')
    },10)
  },
  beforeDestroy () {
    //document.querySelector('body').setAttribute('style', 'background:#ffffff')
  },
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      indexshow:false,
      headerNavData:{
         left:true,
         title:'详情',
         right:false,
         bj:'#2f3650',
         search:false,
         share:false,
         border:false
      },
      coin:getCookieWithDefault('coin','CNY'),
      navbj:'m',
      newdatay:[],
      newdatax:[],
      heigh:'--',
      low:'--',
      volume:'--'
    }
  },
  created(){
    this.headerNavData.title = this.$route.query.name
    this.indexshow=true
  },
  mounted(){
    this.chartdata('m')
  },
  methods:{
    //跳转交易记录详情
    goorder(id){
      this.$router.push({path:'/index_order_detail',query:{id:id,name:this.headerNavData.title}})
    },
    //切换nav图
    tabnar(type){
      this.navbj = type
      this.newdatay=[]
      this.newdatax=[]
      this.chartdata(type)
    },
    chartdata(type){
      var that = this
      this.axios({
        url:this.Url + 'Quota.ashx',
        method:'post',
        params: {req:5,code:this.$route.query.name,type:type,moneycode:this.coin}
      }).then(res=>{
        var data = res.data.data.data
        if (JSON.stringify(res.data.data) != "{}") {
            that.heigh = (res.data.data.high).toFixed(4)
            that.low = (res.data.data.low).toFixed(4)
            that.volume = (res.data.data.volume).toFixed(2)
          for (var i = 0; i < data.length; i++) {
            if (type=='d' || type=='w') {
              that.newdatax.push(data[i].createTime.split("T")[0].substring(5,10))
            }else{
              that.newdatax.push(data[i].createTime.split("T")[1].substring(0,5))
            }
            that.newdatay.push(data[i].currPrice)
          }
          var sum=0;
          for (var i = 0; i < that.newdatay.length; i++){
            sum += parseFloat(that.newdatay[i]);
          }
          var average = (sum/that.newdatay.length).toFixed(4)
          that.drawLine(that.newdatay,that.newdatax,average)
        }else{
          that.drawLine([],[],"")
        }
      }).catch(res=>{
        console.log(res)
      })
    },
    drawLine(newdatay,newdatax,average){
      var that = this
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 绘制图表
        var colors = ['#5793f3', '#d14a61', '#675bba']
        myChart.setOption({
            //backgroundColor: 'rgb(245, 245, 245)',
            title: { 
              //text: '资产数量'
            },
            tooltip: {
                trigger: 'none',
                axisPointer: {
                    type: 'cross'
                }
            },
            legend: {
                show: false,
            },
            grid: [
              {
                  top: 15 + '%',
                  bottom: 10 + '%',
                  left: 4 + '%',
                  right: 3 + '%',
                  height: 65 + '%',
                  containLabel: true,
              }
            ],
            xAxis: {
                type: 'category',
                axisTick: {
                    alignWithLabel: true
                },
                scale: true,
                axisLine: {
                    onZero: false,
                    lineStyle: {
                        color: '#666'
                    }
                },
                boundaryGap : true,
                splitLine: {show: false},
                splitNumber: 20,
                axisLabel: {
                    textStyle: {
                        color: '#666',//坐标值得具体的颜色
                    }
                },
                data: newdatax
            },
            yAxis: {
              type: 'value',
              position:'right',
              scale:true,
              axisLine: {
                  onZero: false,
                  lineStyle: {
                      color: '#666'
                  }
              },
              axisLabel: {
                  textStyle: {
                      color: '#666',//坐标值得具体的颜色
                  }
              },
              axisTick:{
                show:false
              },
              splitLine:{
                show:false
              }
            },
            dataZoom: [
                {
                    type: 'inside',
                    realtime: true
                }
            ],
            series: [
                {
                    name:'',
                    type:'line',
                    //smooth: true,
                    showSymbol: false,
                    lineStyle: {
                        normal: {
                            width: 1,
                            color: '#add8ff',
                        }
                    },
                    areaStyle: {
                      color: {
                          type: 'linear',
                          x: 0,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                              offset: 0, color: 'rgb(192, 220, 245)' // 0% 处的颜色
                          }, {
                              offset: 1, color: 'rgb(51, 51, 51)' // 100% 处的颜色
                          }],
                          globalCoord: false // 缺省为 false
                      }
                    },
                    markLine: {
                      symbol: "none",
                      lineStyle: {
                          normal: {
                              type: 'dashed',
                              width: 1,
                              color:'#ff0000'
                          },
                      },
                      data: [
                        {
                            name: 'name',
                            x: 0,
                            yAxis: average,
                        },
                      ]
                    },
                    data: newdatay
                },
                // {
                //     name:'降水量',
                //     type:'bar',
                //     barWidth:'10',
                //     itemStyle:{
                //       color:'#666'
                //     },
                //     data:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3]
                // },
            ]
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/css/hangqing_detail.css'
</style>



